<script>
	import { mapObj, mapSources } from './stores.js';
	
	export let id;
	export let type;
	export let url = null;
	export let props = {};
	export let data = null;
	export let promoteId = null;
	
	function addSource(source) {
		$mapSources = [...$mapSources, source];
	}
	
	if ($mapObj.getSource(id)) {
    $mapObj.removeSource(id);
	}
	
	$mapObj.on('load', function() {
	
  	if (type == "geojson") {
	  	if (data) {
		  	$mapObj.addSource(id, {
	  		  type: type,
	  		  data: data,
					promoteId: promoteId
	  		});
				addSource(id);
  		} else if (url) {
	  		$mapObj.addSource(id, {
	  		  type: type,
	  		  data: url,
					promoteId: promoteId
		  	});
				addSource(id);
		  }
	  } else if (type == "vector") {
	  	$mapObj.addSource(id, {
	  		type: type,
	  		tiles: [ url ],
				promoteId: promoteId,
	  		...props
  		});
			addSource(id);
  	}
	});
	
</script>