import { csvParse } from 'd3-dsv';
import { bbox } from '@turf/turf';
import { feature } from 'topojson-client';

// Set colour palettes
export const colors = {
	sequential: ['rgb(210, 233, 254)', 'rgb(159, 201, 238)', 'rgb(24, 158, 212)', 'rgb(0, 97, 180)', 'rgb(0, 33, 104)'],
	diverging: []
}

export function adjectify(breaks, rank, words) {
	if (rank < breaks[1]) {
		return 'significantly ' + words[0] + ' than';
	} else if (rank < breaks[3]) {
		return 'somewhat ' + words[0] + ' than';
	} else if (rank < breaks[5]) {
		return 'close to';
	} else if (rank < breaks[7]) {
		return 'somewhat ' + words[1] + ' than';
	} else {
		return 'significantly ' + words[1] + ' than';
	}
}
	
export function plusminus(value, unit, singular = null) {
	singular = singular == null ? unit : singular;
	unit = Math.abs(value) === 1 ? singular : unit;
	if (value > 0) {
		return 'an increase of ' + value + unit + ' compared to 2001';
	} else if (value < 0) {
		return 'a decrease of ' + Math.abs(value) + unit + ' compared to 2001'
	}  else if (value < 0) {
		return 'the same as in 2001'
	}
}

export async function getDataset(url) {
  let response = await fetch(url);
  let string = await response.text();
	let data = await csvParse(string,  function(d) {
		return {
			geocode: d.geocode,
			value: Number(d.value)
		};
	});
	let cuts = getCuts(data);
	data.forEach(d => {
		d.color = getColor(colors.sequential, cuts.breaks, d.value);
	});
  return {
		data: data,
		...cuts
	};
}

export async function getPlace(url) {
  let response = await fetch(url);
  let string = await response.text();
	let data = await JSON.parse(string);
  return data;
}

export async function getGeodata(url, layer) {
  let response = await fetch(url);
  let string = await response.text();
	let topojson = await JSON.parse(string);
	let geojson = await feature(topojson, layer);
  return geojson;
}

export function makeOptions(features) {
	let options = [];
	
	for (let i in features) {
		let option = {
			geocode: features[i].properties['AREACD'],
			name: features[i].properties['AREANM']
		}
		options.push(option);
	}
	options.sort((a, b) => a.name.localeCompare(b.name));
	return options;
}

export function makeLookup(options) {
	let lookup = {};
	
	for (let i in options) {
		lookup[options[i].geocode] = options[i].name;
	}

	return lookup;
}

function getCuts(dataset) {
	dataset.sort((a, b) => a.value - b.value);
	let p20 = Math.floor(dataset.length * 0.2);
	let breaks = [
		dataset[0].value,
		dataset[p20].value,
		dataset[p20 * 2].value,
		dataset[p20 * 3].value,
		dataset[p20 * 4].value,
		dataset[dataset.length - 1].value
	];
	let top5 = [
		dataset[dataset.length - 1],
		dataset[dataset.length - 2],
		dataset[dataset.length - 3],
		dataset[dataset.length - 4],
		dataset[dataset.length - 5]
	];
	let bottom5 = [
		dataset[0],
		dataset[1],
		dataset[2],
		dataset[3],
		dataset[4]
	];
	return {
		breaks: breaks,
		top5: top5,
		bottom5: bottom5
	};
}

export function getColor(colors, breaks, value) {
	for (var i = 1; i < breaks.length; i ++) {
		if (value <= breaks[i]) {
			return colors[i - 1];
		}
	}
}

export function navTo(location, map, geojson) {
	if (location.geocode) {
		let selected = geojson.features.find(obj => obj.properties['AREACD'] === location.geocode);
		let box = bbox(selected.geometry);
		map.fitBounds(box, { padding: 20 });
	} else if (location.lon) {
		map.flyTo({
			center: [
				location.lon,
				location.lat
			],
			zoom: location.zoom
		});
	} else if (location.bounds) {
		map.fitBounds(location.bounds);
	}
}

export function inViewport(el) {
	let rect = el.getBoundingClientRect();
	return rect.top >= -250;
}

export function getPyramid(data) {
	let result = [];
	for (let yr in data) {
		if (yr != 'change') {
			let year = Number(yr);
			for (let grp in data[yr]) {
				let row = {};
				if (grp != 'all') {
					let array = grp.split('-');
					let group = array[1] ? array[0] : grp.substring(0, 2);
					row['year'] = year;
					row['age'] = Number(group);
					row['people'] = data[yr][grp];
			    result.push(row);
				}
			}
		}
	}
	return result;
}