<script>
  import { mapObj } from './stores.js';
	import { getColor } from './utils.js';
	
	export let id;
	export let source;
	export let sourceLayer = null;
	export let type;
	export let layout = {};
	export let paint = {};
	export let data = null;
	export let selected = null;
	export let order = 'boundary_country';
	
	let selectedPrev = null;
	
	if ($mapObj.getLayer(id)) {
    $mapObj.removeLayer(id);
	}
	
	if (type == 'fill') {
		paint = {
			'fill-color': ['case',
			  ['!=', ['feature-state','color'], null],
			  ['feature-state','color'],
			  'rgba(192, 192, 192, 0.5)'
			],
			'fill-opacity': 0.7
		};
	}
	
	let options = {
		'id': id,
		'type': type,
		'source': source,
		'paint': paint,
		'layout': layout
	};
	
	if (sourceLayer) {
		options['source-layer'] = sourceLayer;
	}
	
	$mapObj.addLayer(options, order);
		
	$: if (type == 'fill' && data) {
		for (let i in data.data) {
			if (data.data[i].geocode) {
				$mapObj.setFeatureState({
					source: source,
					id: data.data[i].geocode
				}, {
					color: data.data[i].color
				});
			}
		}
	} else if (type == 'fill' && data == null) {
		let src = $mapObj.getSource(source);
		let promoteId = src.promoteId;
		let features = src._data.features;
		for (let i in features) {
			if (features[i].properties[promoteId]) {
				$mapObj.setFeatureState({
					source: source,
					id: features[i].properties[promoteId]
				}, {
					color: null
				});
			}
		}
	}
	
	$: if (type == 'line') {
		if (selectedPrev) {
			$mapObj.setFeatureState({
				source: source,
				id: selectedPrev
			}, {
				selected: false
			});
		}
		if (selected) {
			$mapObj.setFeatureState({
				source: source,
				id: selected
			}, {
				selected: true
			});
			selectedPrev = selected;
		}
	}
</script>